*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}
.champs {
  position: relative; /* nécessaire pour que l'élément pseudo soit positionné correctement */
  width: 100%;
  height: 100vh;
}

.champs:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url(../public/img/resumes-desk.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.7;
}

h2{
  font-weight: bolder;
  font-style: oblique;
  font-size: 50px;
}
.connect {
  position:relative; 
  z-index: 1; 
  top: 35%;
}
input{
  border-radius: 10px;
  padding: 10px;
  width: 50%;
}
/* CV */

.cv {
  display: flex;
}

hr{
  border-top: 5px solid black;
}

a{
  color: #333;
  text-decoration: underline;
  text-underline-offset: 5px;
  display: inline-block;
  transition: transform 0.3s ease;
}

a:hover {
  color: white;
  text-decoration: underline;
  text-underline-offset: 5px;
  background-color: rgb(111, 154, 185);
  border-radius: 5px;
  padding: 5px;
  transform: scale(1.1);
}

.enTete {
  width:300px;
  background-color:rgb(210, 208, 208);
  padding: 20px;
}

ul{
  list-style-type: none;
  padding: 0;
}

h1{
  font-weight: bolder;
}

.enTete img {
  width: 70%;
  border: solid 2px rgb(111, 154, 185);
  border-radius: 5%;
  margin-bottom: 20px;
}

h5{
  background-color: rgb(111, 154, 185);
  color: white;
  padding: 5px;
  border-radius: 5px;
  margin: 0;
}
.contact{
  margin-top: 15%;
  margin-bottom: 15%;
  text-align: start;
}

.pdf{
  padding: 5px;
  border-radius: 10px;
  border: none;
  background-color: red;
  font-weight: bolder;
  font-style: oblique;
  display: inline-block;
  transition: transform 0.3s ease;
}

.pdf:hover {
  transform: scale(1.1);
}

.corps:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('../public/img/man.jpg');
  background-size: cover;
  background-position: center;
  opacity: 0.2;
}
.corps{
  position: relative;
  width: 100%;
  flex-grow: 1;
  padding: 20px;
}
.corps h1,
.corps h3,
.corps h4,
.corps p,
.corps ul,
.corps hr,
.titre {
  position: relative;
}

.titre{
  padding: 20px;
}

.titre,
h4 {
  background-color: rgb(111, 154, 185);
  color: white;
  border-radius: 5px;
}

h4{
  font-weight: bolder;
  font-style: italic;
  text-decoration: underline;
  padding: 5px;
}
.sep{
  border-top: 2px solid;
  margin-left: 25%;
  margin-right: 25%;
 }

@media(max-width: 796px) {
 
  .curi{
    width: 50%;
  }
  .enTete{
    width: 100%;
    padding: 10px;
    height: 100%;
  }

  h5,
  .contact{
    font-size:40%;
  }

  .pdf{
    font-size: 60%;
    width: 100%;
  }
  .titre{
    padding: 5px;
  }

  .corps {
    padding: 10px;
    font-size: 40%;
  }

  h1{
    font-size: 15px;
  }
  h4,
  h3{
    font-size: 10px;
  }
  .pre,
  .desc{
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }

  hr{
    border-top: 2px solid black;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .sep{
    border-top: 0.5px solid black;
   }

  a,
  a:hover{
    text-underline-offset: 1px;
  }

}